/* ==========================================================================
   WooCommerce Message Responsive styles - begin
   ========================================================================== */

@media only screen and (max-width: $ipad-portrait) {

    .eltdf-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 17px 25px;
        }
    }
}

@media only screen and (max-width: $phone-landscape) {

    .eltdf-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 14px 20px;
            line-height: 2em;
            text-align: center;

            a {
            	float: none;
                display: block !important;
                text-align: center;
                width: 70% !important;
                margin: 0 auto 15px !important;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Message Responsive styles - end
   ========================================================================== */