/* ==========================================================================
   WooCommerce Buttons responsive styles - begin
   ========================================================================== */

@include ipad-landscape {

    .woocommerce-page .eltdf-content,
    div.woocommerce {

        a.added_to_cart,
        input[type="submit"],
        button[type="submit"] {
            line-height: 38px;
            padding: 0 28px;
            letter-spacing: 1px;
        }

        a.button {
            line-height: 50px;
            padding: 0 28px;
            letter-spacing: 1px;
        }
    }
}
/* ==========================================================================
   WooCommerce Buttons responsive styles - end
   ========================================================================== */