/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
    
    .eltdf-woo-single-page {
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 12px 7px;
                    }
                }
            }
        }
    }
}

@include ipad-landscape {
    
    .eltdf-woo-single-page {
        
        .eltdf-single-product-content {
            
            .images,
            .eltdf-single-product-summary {
                float: none;
                width: 100%;
                padding: 0;
            }
            
            .eltdf-single-product-summary {
                margin: 30px 0 0;
            }
        }
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 13px 10px;
                    }
                }
            }
        }
    }
}

@include ipad-portrait {

    .eltdf-woo-single-page {

        &.eltdf-woo-single-thumb-on-left-side {

            .eltdf-single-product-content {

                .images {

                    a {

                        &.woocommerce-main-image {
                            width: 100%;
                            margin: 0;
                        }
                    }

                    .thumbnails {
                        position: inherit;
                        top: auto;
                        left: auto;
                        display: inherit;
                        vertical-align: top;
                        width: auto;
                        height: auto;
                        margin: 0 -10px;

                        a {
                            float: left;
                            width: 25%;
                            margin: 20px 0 0;
                            padding: 0 10px;

                            &:first-child {
                                padding: 0 10px;
                            }
                        }
                    }

                    .woocommerce-product-gallery__image {

                        &:first-child {
                            width: 100%;
                            padding: 0 10px;
                        }
                    }

                    &.woocommerce-product-gallery--with-images {

                        .woocommerce-product-gallery__image {

                            &:not(:first-child) {
                                position: inherit;
                                top: auto !important;
                                left: auto;
                                width: auto;
                                height: auto;
                                padding: 20px 10px 0;

                                a {
                                    height: auto;

                                    img {
                                        height: auto;
                                    }
                                }
                            }
                        }
                    }

                    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
                    @for $i from 0 to length($thumb_3_columns_label) {
                        &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
                            $column_width: 100%/($i+2);

                            .woocommerce-product-gallery__image {

                                &:not(:first-child) {
                                    width: $column_width;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@include phone-landscape {

    .eltdf-woo-single-page {

        &.eltdf-woo-single-thumb-on-left-side {

            .eltdf-single-product-content {

                .images {

                    .thumbnails {

                        &.columns-4 {

                            a {
                                width: 50%;
                            }
                        }
                    }

                    &.woocommerce-product-gallery--columns-4 {

                        .woocommerce-product-gallery__image {

                            &:not(:first-child) {
                                width: 50%;
                            }
                        }
                    }
                }
            }
        }

        .eltdf-single-product-content {

            .images {

                .thumbnails {

                    &.columns-4 {

                        a {
                            width: 50%;
                        }
                    }
                }

                &.woocommerce-product-gallery--columns-4 {

                    .woocommerce-product-gallery__image {

                        &:not(:first-child) {
                            width: 50%;
                        }
                    }
                }
            }
        }

        .woocommerce-tabs {
            ul.tabs {
                li {
                    display: block;
                    width: 100%;
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */